<template>
    <div id="app">
       <div class="box-list">
       <el-table
      :data="tableData"
      border
      style="width: 100%">
      <el-table-column
        prop="name"
        label="文档名称"
        width="180">
      </el-table-column>
      <el-table-column
        prop="brief"
        label="文档简介">
      </el-table-column>
      <el-table-column
        label="查看"
        width="100px">
        <template slot-scope="scope">
        <span class="det" @click="det">{{scope.row.det}}<span></span></span>
        </template>
      </el-table-column>
    </el-table>
       </div>
    </div>
</template>

<script>
export default {
    name:'det',
    props:['sysid'],
    data(){
      return{
        tableData:[]
      }
    },
    methods:{
      det(){//查看详情

      },
      axios(){
        this.$axios.get(`/projectFinal/getOneProjectFinalInfo?sysId=${this.sysid}`).then(res=>{
          console.log(res)
          this.tableData = [];
          var newArr = res.data.data.documentList;
          for(let i = 0;i<newArr.length;i++){
            this.tableData.push({
              name:newArr[i].documentName,
              brief:newArr[i].documentOverview,
              det:'查看文档'
            })
          }
        })
      }
    },
    mounted(){
      this.axios();
    },
    watch:{
      sysid(){
        this.axios();
      }
    }
}
</script>

<style scoped>
  .box-list >>> .el-table .cell{
    text-align: center;
    cursor: pointer;
    }
  .det{
      position: relative;
      display: inline-block;
      width: 100%;
      height: 100%;
      cursor: pointer;
      padding: 12px 0;
    }
    .det span{
      width: 0px;
      position: absolute;
      bottom: 10px;
      transform: translateX(-50%);
      left: 50%;
      transition: .5s all;
      display: inline-block;
      border-bottom: 1.5px solid red;
    }
    .det:hover{
      color: red;
    }
    .det:hover span{
      width: 60px;
    }
</style>



