<template>
  <div id="app">
    <div class="header">
    <span>新增本级项目</span>
    <div class="header-right">
      <el-button size="small" @click="add('addLeve')">新增</el-button>
      <el-button size="small" type="warning" @click="axios()">刷新</el-button>
    </div>
    </div>
    <el-divider></el-divider>
    <!-- 下部列表 -->
    <div class="box-list">
      <el-table
      :data="tableData"
      border
      style="width: 100%">
      <el-table-column
        prop="number"
        label="项目编号"
        width="180">
      </el-table-column>
      <el-table-column
        prop="name"
        label="项目名称">
      </el-table-column>
      <el-table-column
        prop="beginTime"
        label="开始时间">
      </el-table-column>
      <el-table-column
        prop="endTime"
        label="结束时间">
      </el-table-column>
      <el-table-column
        label="查看"
        width="180px">
        <template slot-scope="scope">
        <span class="det" @click="det(scope.row.sysid)">{{scope.row.see}}<span></span></span>
        </template>
      </el-table-column>
    </el-table>
    <!-- 分页 -->
    <div class="page">
    <el-pagination
    :page-size=pagesize
    :current-page=pagenum
    :hide-on-single-page="pds"
    @prev-click = 'upPage'
    @next-click = 'nextPage'
    @current-change	= 'currentPage'
    background
    layout="prev, pager, next"
    :total=totalcount>
    </el-pagination>
  </div>
    </div>
    <!-- 模态框 -->
    <el-dialog
    :visible.sync="dialogVisible"
    width="800px">
    <det :sysid='id'></det><!--查看详情组件-->
    <span slot="footer" class="dialog-footer">
    <el-button @click="dialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="dialogVisible = false">确 定</el-button>
  </span>
</el-dialog>
  </div>
</template>
<script>
import det from './det'
  export default{
    name:'addProject',
    components:{
      det
    },
    data(){
      return{
        tableData:[],
        dialogVisible:false,
        pagenum:1,//第几页
        pagesize:10,//一页显示多少
        totalcount:null,//总条数
        pds:true,//只有一页时是否显示
        id:null,//列表的sysid
      }
    },
    methods:{
      add(zhi){//新增跳转按钮
        this.$router.push({
          name:zhi
        })
      },
      det(sysid){//查看详情
        this.dialogVisible = true;
        this.id = sysid;
      },
      upPage(page){//上一页
        this.pagenum = page;
        this.axios();
      },
      nextPage(page){//下一页
        this.pagenum = page;
        this.axios();
      },
      currentPage(page){//点击数字
        this.pagenum = page;
        this.axios();
      },
      axios(){//列表数据
        this.$axios.get(`/projectFinal/getBenJiList?pageNum=${this.pagenum}&pageSize=${this.pagesize}`).then(res=>{
          console.log(res)
          var newData = res.data.data;
          this.tableData = [];
          this.totalcount = res.data.totalcount;//总条数
          for(let i = 0;i<newData.length;i++){
            this.tableData.push({
              number: newData[i].projectNum,
              name: newData[i].projectName,
              beginTime:newData[i].beginTime,
              endTime:newData[i].endTime,
              sysid:newData[i].sysId,
              see:'查看详情',
            })
          }
        })
      }
    },
    mounted(){
      this.axios();
    }
  }
</script>
<style scoped>
  .header{
      display: flex;
      justify-content: space-between;
      padding: 10px 0;
    }
    .header span{
      vertical-align: middle;
      line-height: 32px;
    }
    .header .header-right{
      vertical-align: middle;
    }
    #app .el-divider--horizontal{
      margin: 0;
    }
    .table-bot >>> .el-table .cell{
      text-align: center;
    }
    /* 下部列表 */
    .box-list >>> .el-table .cell{
    text-align: center;
    cursor: pointer;
    }

    #app >>> .el-table td{
      padding: 0;
    }
    .det{
      position: relative;
      display: inline-block;
      width: 100%;
      height: 100%;
      cursor: pointer;
      padding: 12px 0;
    }
    .det span{
      width: 0px;
      position: absolute;
      bottom: 10px;
      transform: translateX(-50%);
      left: 50%;
      transition: .5s all;
      display: inline-block;
      border-bottom: 1.5px solid red;
    }
    .det:hover{
      color: red;
    }
    .det:hover span{
      width: 60px;
    }
    /* 分页 */
    .page >>> .el-pagination{
    display: flex;
    justify-content: center;
    margin-top: 10px;
    }
</style>



